import Vue from 'vue'
import Router from 'vue-router'
import NotFound from '@/views/NotFound'
import { TokenService } from '@/services/storage.service.js'

Vue.use(Router)

const router =  new Router({
  routes: [
    {
      path: '/login',
      name: 'Login',
      meta: {layout: 'pages'},
      //component:()=> import ('../views/Home.vue')
    },
    {
      path: '/',
      name: 'Home',
      meta: {layout: 'bar'},
      component:()=> import ('../views/Home.vue')
    },
    {
      path: '**',
      name: 'Not-Found  ',
      meta: {layout: 'notfound'},
      //component:NotFound
    }
  ],
  
});

router.beforeEach((to, from, next) => {
  var tkn = TokenService.getToken();
  if(tkn){
    if(to.path == '/login'){
      
      next({path:from.path});
    }else{
      next();
    }
  }
  else if(to.path == '/login' && !tkn){
    next();
  }
  else{
    return next({path:'/login'});
  }
});
export default router;



