// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'
import router from './router'
import { BootstrapVue, IconsPlugin } from 'bootstrap-vue'
import 'bootstrap/dist/css/bootstrap.css'
import 'bootstrap-vue/dist/bootstrap-vue.css'

import Navbar from '@/components/Navbar/Navbar.vue'
import Login from '@/views/Login.vue'
import NotFound from '@/views/NotFound.vue'
import ApiServices from './services/api.service'

//const server = "http://localhost:8000";
const server = "http://ec2-18-223-113-64.us-east-2.compute.amazonaws.com:8000"
Vue.use(BootstrapVue)
Vue.use(IconsPlugin)
Vue.component('pages-layout', Login);
Vue.component('bar-layout', Navbar);
Vue.component('notfound-layout', NotFound);
Vue.config.productionTip = false
ApiServices.init(server)//(process.env.VUE_APP_ROOT_API)

/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  components: { App },
  template: '<App/>'
})



