<template>
  <div class="container">
      <b-card  title="Iniciar sesión" header-tag="header" 
      footer-tag="footer" style="max-width: 50rem; text-color:red;"
      class="mb-2 bg-slick-carbon">
        <b-form @submit="onSubmit">
            <b-form-group
              id="input-group-1"
              
              label-for="input-1"
            >
              <b-form-input
                id="input-1"
                v-model="form.username"
                type="text"
                required
                placeholder="Correo electrónico o nombre de usuario"
              ></b-form-input>
            </b-form-group>
      
            <b-form-group id="input-group-2"  label-for="input-2">
              <b-form-input
                id="input-2"
                v-model="form.password"
                required
                type="password"
                placeholder="Contraseña"
              ></b-form-input>
            </b-form-group>
            <span v-if="error" style="color:red;">El usuario o contraseña son incorrectos</span>
            <b-button block  variant="primary">Iniciar sesión</b-button>
            <hr>
            <b-button  variant="success">Crear cuenta nueva</b-button> <!-- type="submit"-->
            <br>
            <br>
            <b-button @click="login" size="sm" class="mb-2 " variant="outline-light">Iniciar con BBVArchuletas</b-button>
        </b-form>
        <b-alert
          :show="dismissCountDown"
          dismissible
          :variant="status"
          @dismissed="dismissCountDown=0"
          @dismiss-count-down="countDownChanged"
        >
          {{message}}
        </b-alert>
        
      </b-card>

  </div>

</template>

<script>
import ApiService from '@/services/api.service.js';
import { TokenService } from '@/services/storage.service.js';
import { urls } from '@/api.urls.js';
import router from '@/router/index.js';
export default {
  name: 'Login',
  data () {
    return {
      error:false,
      form:{
        "password":"",
        "username":"",
      },
      dismissSecs: 30,
      dismissCountDown: 0,
      message:'',
      status:''
      
    }
  },
  
  methods: {    
                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                  
      login(){
        
        TokenService.saveToken(this.form.username);
        ApiService.post(urls.login, this.form).then(response=>{
           
          if(!response.data.ok){
            this.message = "Este correo no esta registrado"
            this.status = "danger"
          }else{
            this.message = `Hola ${response.data.name}, te hemos enviado un correo electronico`
            this.status = "info"
            router.push("/");
            
          }
          this.showAlert();
          
        });
      },
      onSubmit(evt) {
        ApiService.post(urls.login, this.form).then(response=>{
          console.log(response.data);
          if(response.data.ok){
            this.error = false;
            TokenService.saveToken(response.data.token);
            router.push('/'); 
          }else this.error = true;
        });
      },
      countDownChanged(dismissCountDown) {
        this.dismissCountDown = dismissCountDown
      },
      showAlert() {
        this.dismissCountDown = this.dismissSecs
      }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h1, h2 {
  font-weight: normal;
}
ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: inline-block;
  margin: 0 10px;
}
a {
  color: #42b983;
}
.bg-slick-carbon {
  background-image: linear-gradient(to right, #4facfe 0%, #00f2fe 100%) !important;
}
</style>



